import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { useSelector } from 'react-redux';


function High() {
    const currentPrice = useSelector((state) => state.currentPrice);
    const worstTimeRange = useSelector((state) => state.worstTimeRange);
    return (
        <div className="text-center">
            <Row>

                <Col>Jrgmine tiputund on</Col>
            </Row>
            <Row>
                <Col>{`${worstTimeRange.from}:00st ${worstTimeRange.until}:00ni`}</Col>
            </Row>
            <Row>
                <Col>Siis on kilovatt-tunni hind {currentPrice} eur,
                    mis on {Math.round(100 - worstTimeRange.worstPrice / currentPrice * 100)}% kallim kui praegu
                </Col>
            </Row>
            <Row>
                <Col>Soovitame tiptundide ajal vhendada elektri tarbimist, et aidata kaasa Euroopa hisele eesmrgile alandada tiputundidel -5% elektri tarbmist ja vhendada maagaasi nudlust. Loe lhemalt</Col>
            </Row>
        </div>
    );
};

export default High;
